.form-container {
  width: 100%;
  height: 100vh;
}

form {
  width: 90%;
  height: 40vh;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 20px;
}
form h3 {
  display: flex;
  align-items: center;
  width: 100%;
  text-align: left;
  height: 50px;
  font-size: 1.5rem;
}
form h3 span {
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
}
form h3 span svg {
  height: 25px;
}

.input-container {
  width: 100%;
}

.input-container input {
  width: 95%;
  height: 50px;
  border-radius: 10px;
  padding-left: 20px;
}
.login-btn {
  width: 100%;
}
.input-field {
  display: flex;
}
.login-btn p {
  width: 100%;
  text-align: center;
}
.loginBtn {
  width: 100%;
  height: 50px;
  border-radius: 10px;
  cursor: pointer;
  font-size: 1.5rem;
  font-weight: bold;
}

/* Register */

#create-account {
  width: 90%;
  height: 80vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 10px;
}

.create-input {
  width: 100%;
  display: flex;
  flex-direction: column;
}

.input-field {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.input-field input {
  width: 95%;
  height: 50px;
  border-radius: 10px;
  padding-left: 10px;
}

.dateofbirth {
  display: flex;
  flex-direction: row-reverse;
  justify-content: start;
  align-items: center;
}

.dateofbirth input {
  width: 400px;
}
.dateofbirth span {
  width: 100px;
}

.creat-login {
  width: 95%;
  height: 50px;
  border-radius: 10px;
  font-weight: bold;
}
@media only screen and (min-width: 700px) {
  form {
    width: 626px;
  }

  .Register {
    max-width: 700px;
  }
  .dateofbirth input {
    width: 80%;
  }
}
