.blog-container {
  width: 100%;
  height: 100vh;
  margin: 0 auto;
}
.section-container {
  background-color: rgb(239, 239, 243);
}
section {
  width: 100%;
  height: 100vh;
  margin-bottom: 20px;
  overflow: hidden;
  padding-top: 20px;
}

#hero {
  display: flex;
  background-color: rgb(255, 255, 255);
  align-items: center;
  flex-direction: column;
  gap: 20px;
}

.left {
  aspect-ratio: 9/10;
  height: 500px;
  border-radius: 20px;
  overflow: hidden;
}
.left img {
  mix-blend-mode: multiply;
  width: 100%;
  height: 100%;
}

.right {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 20px;
}

.right h1 {
  font-size: 2rem;
  text-decoration: underline;
  width: 80%;
  color: rgb(8, 8, 8);
  font-family: sans-serif;
  text-align: left;
}

.right p {
  width: 80%;
  font-size: 1.2rem;
  color: rgb(5, 5, 5);
}

/* about */

#about {
  width: 100%;

  background-color: rgb(186, 186, 194);
}

.about-container {
  display: flex;
  flex-direction: column-reverse;
  align-items: center;
  justify-content: center;
  gap: 20px;
}
.mid-container-img {
  aspect-ratio: 9/10;
  height: 500px;
  mix-blend-mode: multiply;
}

.mid-container-img img {
  width: 100%;
  height: 100%;
}

#gallery {
  background-color: rgb(135, 139, 139);
}

#gallery ul {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
#gallery ul li {
  aspect-ratio: 9/10;
  height: 500px;
  border-radius: 20px;
  overflow: hidden;
}
#gallery ul li img {
  mix-blend-mode: multiply;
  width: 100%;
  height: 100%;
}

@media only screen and (min-width: 1024px) {
  section {
    height: 70vh;
  }
  #hero {
    flex-direction: row;
  }

  #about {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .left {
    width: 800px;
    height: 500px;
  }

  .about-container {
    flex-direction: row-reverse;
  }
  .mid-container-img {
    width: 900px;
  }
  #gallery ul {
    flex-direction: row;
  }
}

@media only screen and (min-width: 1884px) {
  section {
    height: 100vh;
  }

  .left {
    width: 1200px;
  }
  .mid-container-img {
    width: 1200px;
  }
}
