.cart-container {
  width: 100%;

  background-color: rgba(2, 9, 48);
  gap: 20px;
}

.cart-container h2 {
  margin: 0 auto;
  width: 80%;
  height: 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: white;
}

.title {
  width: 200px;
  overflow-wrap: break-word;
}
h2 span {
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
}
h2 svg {
  width: 30px;
  height: 30px;
}
.item-basket {
  width: 80%;
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin: 0 auto;
}

.item-basket li {
  height: 100px;
  width: 100%;
  list-style: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: white;
}
.cart-image {
  display: flex;
  align-items: center;
  width: 60px;
  height: 60px;
  border-radius: 10px;
  overflow: hidden;
}
.cart-image img {
  width: 100%;
  height: 100%;
}

.cart-title {
  display: flex;
  gap: 10px;
}
.total-price {
  border-top: 1px solid white;
}

.check-out {
  width: 100%;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.check-out {
  font-size: 1.2rem;
  background-color: white;
  padding: 4px;
  border-radius: 10px;
  font-weight: bold;
}
