.product-container {
  width: 100%;
  height: 100%;
}

.prod-list {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
  width: 100%;
  height: 100%;
  flex-wrap: wrap;
}

.prod-cards {
  width: 600px;
  height: 700px;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.prod-cards button {
  height: 30px;
}

.prod-cards h3 {
  font-size: 1.2rem;
  margin: 0;
  color: rgb(64, 64, 128);
}

.prod-cards p {
  margin: 0;
}
.prod-image {
  width: 100%;
  height: 400px;
}

.prod-image ul {
  width: 100%;
  height: 100%;
  display: flex;
  overflow: hidden;
  padding: 0;
  position: relative;
}

.arrow-container {
  position: absolute;
  width: 80%;
  height: 100%;
  left: 10%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.arrow-container span {
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(83, 116, 224, 0.5);
  border-radius: 10px;
  color: white;
  opacity: 0;
  transition: 0.5s ease-in;
}
.arrow-container:hover span {
  opacity: 1;
  outline: 1px solid rgb(14, 13, 13);
}

.prod-image ul li {
  list-style-type: none;
  flex: 1 0 100%;
  height: 100%;
}

.prod-image ul li img {
  width: 100%;
  height: 100%;
}

.brand {
  font-size: 1rem;
  font-weight: bold;
  margin: 0;
}
.price {
  font-size: 1.8rem;
  font-weight: bold;
  color: rgb(64, 64, 226);
  margin: 0;
}

.ratings {
  display: flex;
  flex-direction: column;

  gap: 10px;
}
.ratings p {
  width: 100%;
  height: 50px;
  display: flex;
  justify-content: space-between;

  gap: 10px;
}
.ratings p h3 {
  color: green;
}
.ratings button {
  width: 80%;
  height: 50px;
  margin: 0 auto;
  border-radius: 10px;
  font-size: 1.2rem;
  cursor: pointer;
  background-color: rgb(51, 51, 105);
  color: white;
  font-weight: bold;
}

.ratings-star {
  color: rgb(8, 100, 221);
}

.info-container {
  width: 80%;
  margin: 0 auto;
}
