nav {
  width: 90%;
  height: 100px;

  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
}

nav h1 {
  display: none;
}

.search-container {
  display: flex;
  height: 54px;
}

.search-container input {
  width: 100px;
  height: 50px;
  border-top-left-radius: 20px;
  border-bottom-left-radius: 20px;
  padding-left: 20px;
  font-size: 1.2rem;
  font-family: sans-serif;
  font-weight: bold;
}
.user-container {
  display: none;
}
nav ul {
  display: flex;
  width: 200px;
  gap: 10px;
  justify-content: center;
  align-items: center;
  height: 100%;
  padding: 0;
  margin: 0;
}
nav ul li {
  list-style: none;
  font-weight: bold;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1rem;
}
nav ul li .user-container span {
  width: 100%;
  display: flex;
  justify-content: space-between;
}
nav ul li:nth-child(2) {
  display: flex;
  flex-direction: column-reverse;
  align-items: center;
  justify-content: center;
}
svg {
  width: 25px;
  height: 25px;
  cursor: pointer;
}

.search-icon {
  background-color: black;
  color: white;
  width: 50px;
  height: 54px;
  border-bottom-right-radius: 20px;
  border-top-right-radius: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
}

@media only screen and (min-width: 540px) {
  .search-container input {
    width: 200px;
  }
}

@media only screen and (min-width: 768px) {
  .search-container input {
    width: 400px;
  }
  nav h1 {
    display: block;
    font-size: 1.2rem;
  }
}

@media only screen and (min-width: 768px) {
  .search-container input {
    width: 300px;
  }
}

@media only screen and (min-width: 896px) {
  .search-container input {
    width: 300px;
  }
}

@media only screen and (min-width: 1200px) {
  .search-container input {
    width: 500px;
  }
}
