.App-container {
  height: 100vh;
}

header {
  display: flex;
  align-items: center;
}
main {
  position: relative;
  height: 80vh;
  overflow: hidden;
  overflow-y: scroll;
  scroll-behavior: smooth;
}

footer {
  border-top: 2px solid white;
  height: 15vh;
  background-color: black;
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
